<template>
    <div class="notes">
        <div class="container">
            <div class="notes-nav">
                <h2 v-if="search.length === 0">{{ notes.length ? words.infobar[lang] : words.noinfobar[lang] }}</h2>
                <h2 v-if="search && notes.length > 0">{{words.appbarseacrch[lang]}}</h2>
                <h2 v-if="search && notes.length === 0">{{words.notfound[lang]}}</h2>
                <button @click="grid = !grid">
                    <img src="@/assets/img/grid.svg" alt="" v-if="grid">
                    <img src="@/assets/img/list.svg" alt="" v-else>
                    <span>{{ grid ? words.grid[lang] : words.list[lang] }}</span>
                </button>
            </div>
            <transition-group tag="div" class="notes-grid" name="notes" :class="{column: grid}">
                <NoteItem 
                    :lang="lang"
                    v-for="(note, index) in notes"
                    :key="index"
                    :note="note"
                    @deleteNote="$emit('deleteNote', note.id)"
                    @changeNote="$emit('changeNote', note.id)"
            />
            </transition-group>
        </div>
    </div>
</template>
<script>
import NoteItem from './NoteItem.vue'
export default {
  props: ['notes', 'search', 'lang'],
  inject: ['words'],
  components: { NoteItem },
  data() {
    return {
        grid: false
    }
  },
    
}
</script>
