<template>
    <Transition name="modal">
        <div class="modal" @click="$emit('closeModal')">
            <div class="modal-content" @click.stop>
                <h2>{{edit ? words.titlewindowedit[lang] : words.titlewindow[lang]}}</h2>
                <div>
                    <input type="text" required placeholder="Title" v-model="user.title" v-if="!edit">
                    <input type="text" required placeholder="Title" v-model="editObj.title" v-else>
                    <textarea required placeholder="Content" v-model="user.descr" v-if="!edit"></textarea>
                    <textarea required placeholder="Content" v-model="editObj.descr" v-else></textarea>
                    <div class="modal-btns">
                        <a href="#" class="btn cancel" @click.prevent="$emit('closeModal')">{{words.closebtn[lang]}}</a>
                        <button @click.prevent="send" type="button" class="btn add" v-if="!edit">{{words.addbtn[lang]}}</button>
                        <button @click="changeNote" type="button" class="btn add" v-else>{{words.editwindowbtn[lang]}}</button>
                    </div>
                </div>
            </div>
        </div>
    </Transition>
</template>

<script>
    export default {
        props: ['currentId', 'editObj', 'edit', 'lang'],
        inject: ['words'],
        data() {
            return {
                user: {
                    title: '',
                    descr: '',
                }
            }
        },
        methods: {
            send(){
                this.$emit('addNote', {...this.user})
                this.$emit('closeModal')
                for (const key in this.user) this.user[key] = ''
            },
            changeNote(){
                if(this.user.title != '' && this.user.descr != ''){
                    const editNote = {
                        id: this.editObj.id,
                        title: this.user.title,
                        descr: this.user.descr,
                        date: new Date().toLocaleString()
                    }
                    this.$emit('editedNote', editNote)
                }
                this.$emit('closeModal')
            }
        },
    }
</script>

