<template>
<header class="header">
    <transition-group name="header-notes">
     <div class="header-notes" v-if="header == true"> 
        <button @click="changeLang" v-if="lang == 'ru'">UZ</button>
        <button v-else @click="changeLang">RU</button>
        <h1>{{words.appbartitle[lang]}}</h1>
        <button @click="header = false"><img src="@/assets/img/search.svg" alt=""></button>
    </div>
    <div class="header-form" v-if="header == false">
     <img src="@/assets/img/back.svg" alt="" @click="header = true, search = ''">
        <input type="text" :placeholder="words.appbarseacrch[lang]" v-model="search" required>
        <button @click="search = ''"><img src="@/assets/img/reset.svg" alt=""></button>
    </div>
    </transition-group>
   
</header>

</template>

<script>
    export default {
        props: ['lang'],
        inject: ['words'],
        data() {
            return {
                search: '',
                header: true
            }
        },
        watch: {
            search(val) {
                this.$emit('searchValue', val)
            }
        },
        methods: {
            changeLang(){
                this.$emit('changeLang', this.lang == 'ru' ? 'uz' : 'ru')
            }
        },
    }
</script>

<style lang="scss" scoped>

</style>